enum AUX {
  // AUX1,
  AUX2 = 1,
  AUX3,
  AUX4,
  AUX5,
  AUX6,
  AUX7,
  AUX8,
}

enum SWITCH_TYPE {
  POS2,
  POS3,
  POS6,
}

enum UART {
  UART1,
  UART2,
  UART3,
  UART4,
  UART5,
  UART6,
}

enum PROTOCOL {
  SMART_AUDIO = "2048",
  TRAMP = "8192",
}

